// out: ../css/styles.css, compress: true, strictMath: true

$btnBGColorNormalLight: #3deb94;
$btnBGColorNormalDark: #169616;
$textLight: #fff;
$black: #000;
$textColor: #069;

$wpBlue: #21759b;
$wpOrange: #d54e21;
$wpGrey: #464646;

$colorOk: #169616;
$colorKo: #f59702;
$colorFail: #b92326;

$colorApache: #fa7013;
$colorNginx: #117a11;

body {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1em;
    /*background-image: radial-gradient(circle at 50% 35%, #ffffff, #dddddd);*/
    margin: 0;
    /*transition: all 2s;*/
    user-select: none;
    /*border: 1px solid red;*/
}

h1 {
    font-size: 140%;
}

h2 {
    font-size: 130%;
}

h3 {
    font-size: 120%;
}

h4 {
    font-size: 110%;
}

h1, h2, h3, h4, h5 {
    text-align: center;
    color: $wpBlue;
    -webkit-margin-before: 0;
    -webkit-margin-after: 0;
}

p {
    -webkit-margin-before: 0.5em;
    -webkit-margin-after: 0.5em;
}

a {
    text-decoration: none;
    color: $wpBlue;
    border-bottom: 1px dotted $wpBlue;

    &:hover {
        text-decoration: none;
        color: $wpOrange;
        border-bottom: 1px dotted $wpOrange;
    }
}

.without_border {
    border: none;
    text-decoration: none;

    &:hover {
        border: none;
        text-decoration: none;
    }
}

select {
    padding: 5px;
    margin: 10px 0;
    border: 1px solid $wpBlue;
}

fieldset {
    border: 1px solid $wpBlue;
    margin-bottom: 10px;
    padding: 5px 20px 20px 20px;

    legend {
        padding: 0 10px;
        color: $wpBlue;
        font-size: 120%;
        font-weight: 600;
    }
}

#plugin_info {
    border: 2px solid $wpBlue;
    padding: 5px;
    text-align: center;
    border-radius: 8px;
    background-color: #fff;

    p {
        font-size: 120%;
    }
}

#textarea_tab {
    background-color: red;
}

#total-wp-tools {
    background-color: #ebebeb;
    width: 610px; /* until 750px;*/
    height: 550px; /* until 550px */
}

#main {
    padding: 10px;
}

#data {
    margin-top: 1em;
    overflow: auto;
    max-height: 450px;

    height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 1s linear;
}

#primary_menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
    background-color: #fff;
    box-shadow: 3px 3px 3px 0px rgba(0,0,0,0.2);
    border-radius: 3px;

    .item {
        a {
            text-decoration: none;
            border: none;
            &:hover {
                text-decoration: none;
                border: none;
            }
        }
        a > img {
            filter: grayscale(100%);

            &:hover {
                filter: none;
            }
        }
    }
}

#menu_wpo_tools {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;

    .item_wpo {
        cursor: pointer;
        font-weight: 600;
        padding: 5px 10px;
        color: $wpBlue;
        border: 4px solid $wpBlue;
        border-radius: 8px;
    }
}

#http_headers {
    border: 1px solid $black;
    background-color: fadeout( $wpGrey, 80% );
    padding: 5px;
    font-size: 80%;

    .http_header {
        font-weight: 600;
    }

    .http_header_value {
        font-style: italic;
        overflow-wrap: break-word;
        word-wrap: break-word;
        word-break: break-all;
        hyphens: auto;
    }
}

.txt_notice {
    font-size: 90%;
    font-style: italic;
}

.wpo_fieldset {
    display: none;

    .list_item {
        line-height: 2em;
    }
}

.visible {
    display: block;
}

.btn_light {
    border: 3px solid $wpBlue;
    border-radius: 6px;
    font-weight: 600;
    padding: 5px 15px;
    text-decoration: none;

    &:hover {
        border: 3px solid $wpOrange;
        color: $wpOrange;
    }
}

.btn {
    margin-top: 10px;
    border: none;
    cursor: pointer;
    background: $btnBGColorNormalDark;
    /*background-image: -webkit-linear-gradient(top, #3deb94, #169616);
    background-image: -moz-linear-gradient(top, #3deb94, #169616);
    background-image: -ms-linear-gradient(top, #3deb94, #169616);
    background-image: -o-linear-gradient(top, #3deb94, #169616);*/
    /* background-image: linear-gradient(to bottom, $btnBGColorNormalLight, $btnBGColorNormalDark); */
    /*-webkit-border-radius: 6;
    -moz-border-radius:$6;*/
    border-radius: 6px;
    /*-webkit-box-shadow: 0px 1px 3px #666666;
    -moz-box-shadow: 0px 1px 3px #666666;*/
    box-shadow: 0px 1px 3px #666666;
    color: #ffffff;
    font-weight: 600;
    padding: 10px 15px;
    text-decoration: none;

    &:hover {
        background: $btnBGColorNormalLight;
        color: #000;
        /*background-image: -webkit-linear-gradient(top, #169616, #3deb94);
        background-image: -moz-linear-gradient(top, #169616, #3deb94);
        background-image: -ms-linear-gradient(top, #169616, #3deb94);
        background-image: -o-linear-gradient(top, #169616, #3deb94);*/
        /* background-image: linear-gradient(to bottom, $btnBGColorNormalDark, $btnBGColorNormalLight);*/
        text-decoration: none;
    }
}

.url {
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-all;
    hyphens: auto;
}

.full_width {
    display: block;
    width: 100%;
}

.favicon {
    max-width: 64px;
    float: left;
    margin: 0.5em 1em 0.5em 0;
}

.fail {
    color: $colorFail;
}

.ok {
    color: $colorOk;
}

#url_info, #api_url_info {
    p {
        font-size: 90%;
    }

}

.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;

    .tooltiptext {
        visibility: hidden;
        width: auto;
        max-width: 550px;
        background-color: #666;
        color: #fff;
        text-align: center;
        border-radius: 6px;
        padding: 5px;
        position: absolute;
        z-index: 1;
        top: 150%;
        left: 50%;
        margin-left: -60px;

        &:after {
            content: "";
            position: absolute;
            bottom: 100%;
            left: 50%;
            margin-left: -5px;
            border-width: 5px;
            border-style: solid;
            border-color: transparent transparent #666 transparent;
        }
    }

    &:hover .tooltiptext {
        visibility: visible;
    }
}

#notice {
    width: 80%;
    margin: 10px auto;
    padding: 10px;
    border: 1px solid #ccc;
    background-color: rgba(61, 235, 148, 0.21);
    border-radius: 10px;
    text-align: center;
    font-weight: 600;
    display: none;
}

.item_hosting {
    margin: auto;
    text-align: center;
}

.code {
    margin: auto;
    margin-bottom: 22px;
    font-family: Consolas, Monaco, "Courier New", Courier, monospace;
    font-size: 12px;
    font-weight: inherit;
    overflow-x: auto;
    white-space: pre-wrap;
    word-wrap: break-word;
    background: #f3f3f7;
    border: 1px solid #dedee3;
    padding: 5px;
    overflow: auto;
    width: 95%;
}

textarea.code {
    min-height: 200px;
}

.icon_copy {
    float: right;
    margin-right: 16px;
    cursor: pointer;
    filter: grayscale(100%);
        &:hover {
            filter: none;
        }
}

#slider_apache_nginx {
    margin-top: 15px;
    text-align: center;
    color: $wpBlue;
    font-weight: 800;
    font-size: 1.6em;
}

.switch {
    margin-left: 10px;
    margin-right: 10px;
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;

    input {
        display:none;
    }
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $colorApache;
    -webkit-transition: .4s;
    transition: .4s;

    &:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
      }
  }

  input:checked + .slider {
    background-color: $colorNginx;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px $colorNginx;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  .container {
    position: relative;
    display:inline-block;
}
